* {
  box-sizing: content-box;
}

body {
  background: url("OIF.29dd8149.jpeg") center / cover no-repeat fixed;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 20px;
}

header {
  background-color: #495057;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 5px 10px;
  display: flex;
  box-shadow: 0 3px 10px #122d864d;
}

nav ul {
  gap: 15px;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  cursor: pointer;
}

nav ul li:hover {
  color: #a3b18a;
}

.inputSearch {
  background-color: gray;
  border: 1.5px solid gray;
  border-radius: 35px;
  align-items: center;
  width: 400px;
  height: 40px;
  display: flex;
  overflow: hidden;
}

.inputSearch input {
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 0% 15px;
  font-size: 16px;
}

.inputSearch span {
  color: #fff;
  background-color: gray;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 95%;
  font-size: 18px;
  display: flex;
}

.menuBtn {
  cursor: pointer;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  font-size: 20px;
  display: flex;
}

.menuBtn:hover {
  color: #fff;
  background-color: #a3b18a;
}

.desktop {
  display: none;
}

@media (width >= 700px) {
  header {
    padding: 5px 30px;
  }

  .desktop {
    display: flex;
  }

  .menuBtn {
    display: none;
  }
}

.mobile {
  background-color: #fff;
  width: 100%;
  max-width: 700px;
  height: calc(100% - 150px);
  position: absolute;
  top: 120px;
}

.mobile nav, .mobile nav ul {
  flex-direction: column;
}

.mobile .inputSearch {
  width: 95%;
  height: 35px;
}

.hidden {
  display: none;
}

@media (width >= 700px) {
  .mobile {
    display: none;
  }
}

main {
  flex-direction: column;
  gap: 20px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.card {
  border: 2px solid #fff;
  border-radius: 5px;
  flex-direction: column;
  max-width: 400px;
  transition: transform .3s;
  display: flex;
  overflow: hidden;
  box-shadow: 7px 7px 25px 1px #0000000f;
}

.card:hover {
  border-color: #a3b18a;
  transform: scale(1.05);
}

.card img {
  background-color: #6c757d;
  min-height: 200px;
  max-height: 210px;
}

.card h4, .card div {
  margin: 0;
  padding: 5px 10px;
}

.politics {
  color: #929292;
  align-items: center;
  display: flex;
}

.politics p {
  margin: 0;
  font-size: 14px;
}

.politics span {
  padding: 0 10px;
}

.desc {
  -webkit-box-orient: vertical;
  margin: 0;
  display: -webkit-box;
  overflow: hidden;
}

@media (width >= 700px) {
  main {
    flex-flow: wrap;
    justify-content: center;
    padding: 20px;
  }

  .card h4, .card div {
    padding: 5px 15px;
  }
}
